@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Playfair+Display:ital@1&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Orbitron&display=swap);
:root {
  --primary-white: rgb(255, 255, 255);
  --primary-dark: rgba(255, 255, 255, 0);
  --primary-blue: rgb(52, 168, 235);
  --header-font: "Playfair Display", serif;
  --typed-text-font: "Orbitron", sans-serif;
}

body {
  font-family: "Roboto", sans-serif;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 80vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

/* NAVBAR */

/* MENU LINK */
.navbar .navbar-brand {
  font-size: 2rem;
  font-style: italic;
}

.nav-link {
  /* font-weight: 500; */
  letter-spacing: 0.0375rem;
}

nav ul li {
  text-transform: uppercase;
}

.navbar-nav > li > a {
  /* margin-right: 0.75rem; */
  margin-left: 0.75rem;
  border-top: 0.1875rem solid var(--primary-dark);
}

/* .nav-item {
  border-top: 0.1875rem solid var(--primary-blue);
} */

.navbar-nav > li > a:hover {
  border-top: 0.1875rem solid var(--primary-white);
}
.navbar-nav > li > a:focus {
  border-top: 0.1875rem solid var(--primary-blue);
}

/************************* MEDIA QUERIES ************************/
@media (max-width: 400px) {
  .navbar,
  .navbar-brand {
    font-size: 0.5rem;
    display: block;
  }
}

.header-wrapper {
  position: relative;
  background-image: url(/react-portfolio/static/media/mainBG.600ba5b3.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  height: 80vh;
}

.main-info {
  display: flex;
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;

  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.main-info h1 {
  font-family: var(--header-font);
  color: var(--primary-white);
  text-transform: uppercase;
  font-size: 3rem;
  text-shadow: 1px 3px #000;
  /* font-weight: bolder; */
  /* letter-spacing: 10px; */
}

.typed-text {
  font-family: var(--typed-text-font);
  font-size: 2rem;
  color: var(--primary-blue);
  letter-spacing: 2px;
  background-color: #000;
  border-radius: 20px;
  padding: 0 10px;
  margin-top: 3rem;
}

.btn-resume {
  margin-top: 3rem;
  border-radius: 0.625rem;
  font-family: var(--header-font);
  font-weight: bold;
  font-size: 2rem;
  letter-spacing: 0.25rem;
  border: 2px solid var(--primary-white);
}

.about-photo > img {
  height: 25rem;
  border-radius: 50%;
  padding: 0.3rem;
  border: 1px solid var(--primary-blue);
}

.about-heading {
  color: var(--primary-blue);
  text-transform: uppercase;
}

.about-text {
  font-size: 1.25rem;
  height: 66vh;
}

.portfolio-heading-wrapper h1 {
  color: var(--primary-blue);
  text-transform: uppercase;
  font-size: 2.5rem;
}

.portfolio-heading-text {
  /* font-family: var(--header-font); */
  font-size: 1.5rem;
}

.container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.experience-heading{
  color: var(--primary-blue);
  text-transform: uppercase;
  text-align: center;
}
.form {
  width: 100%;
  height: 66vh;
}

.heading h1 {
  text-transform: uppercase;
  color: var(--primary-blue);
}

.footer {
  background-color: #343a40;
  color: var(--primary-white);
}

.footer a {
  color: var(--primary-white);
}

.footer a:hover {
  color: var(--primary-blue);
  text-decoration: none;
}

.footer {
  justify-content: center;
}

